@import "./src/styles/index";

.opportunities {
    width: 85%;
    padding: 15px $margin;
    margin-top: 30px;
}

.opportunitiesTitle{
    width: 75%;
    margin: 0;
    margin-bottom: 60px;
}

.pTitle {
    margin-top: 28px;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    width: 100%;
}


.gridHero {
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(400px, 1fr));
    grid-column-gap: 60px;
    grid-row-gap: 60px;
    width: 100%;
}


@media (max-width: 1000px) {
    .opportunities {
        width: 85%;
        padding: 0;
        margin-left: $margin-mobile;
    }
    
}


@media (max-width: 740px) {

    .gridHero {
        grid-template-columns: 1fr;
        grid-column-gap: 40px;
        grid-row-gap: 40px;
        width: 100%;
    }

}