@import "./src/styles/index";

.dashboard {
	margin: 50px $margin 50px $margin;
}

.opportunitiesWrapper {
	margin-top: $margin;
	border-top: solid $primary-color 1px;
	padding-top: 10px;
}

.button {
	padding: 15px 30px;
}

.button:hover {
	color: white;
}

.headlineAndButton {
    margin-top: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.h4 {
	display: inline-block;
	margin-bottom: 50px;
}

@media (max-width: 1000px) {
	.dashboard {
		margin: 50px $margin-mobile 50px $margin-mobile;
	}
}

@media (max-width: 700px) {
	.button {
		padding: 5px 10px;
	}
}

@media (max-width: 550px) {
    .headlineAndButton {
        display: block;
    }

    .h3 {
        margin-bottom: 20px;
    }
}
