@import "./src/styles/index";
@import "./src/styles/form";

.page {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    flex-grow: 0;
}

.imgContainer {
    margin-right: 50px;
    width: 60%;
}


@media (max-width: 1150px) {
    .page {
        width: 100%;
        display: block;
    }
    .imgContainer {
        margin-right:0;
    }
}