@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@300;400;600;700&display=swap');


$font: 'Overpass', sans-serif;
$primary-color: black;
$secondary-color-orange: #FFE2B4;
$secondary-color-green: #84D0AB;
$secondary-color-grey: #7A7A7A;
$line-height: 1.5;
$margin: 100px;
$margin-mobile: 30px;
$primary-font-size: 16px;
$h3: 48px;
$h3-mobile: 30px;
$h1: 24;
$h1-mobile: 20px;;
$h2: 18px;
$h2-mobile: 15px;
$error: red;
$error-border: darkred;
$link: black;




body {
  margin: 0;
  font-family: $font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $primary-color;
  font-size: $primary-font-size;
  line-height: $line-height;
}


a {
  transition: all 0.05s ease-out;
  color: $primary-color;
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: none;
}

button {
  color: $primary-color;
  font-size: inherit;
  background-color: transparent;
  border-radius: 7px;
  border: $primary-color solid 1px;
  font-family: $font;
  font-weight: 600;
  font-size: $primary-font-size;
  cursor: pointer;
  transition: all 0.15s ease-out;
}

button:hover {
  background-color: $primary-color;
}

h1, h2, h3, h4, h5 {
  line-height: 1;
  margin: 0;
}

h3 {
  font-size: $h3;
}

h1 {
  font-size: $h1;
}

h2 {
  font-size: $h2;
}

@media (max-width: 700px) {
  h3 {
    font-size: $h3-mobile;
  }
}

@media (max-width: 425px) {
  h1 {
    font-size: $h1-mobile;
  }
  h2 {
    font-size: $h2-mobile;
  }
  
}