@import "./src/styles/index";

.selectionNav {
    margin-top: 10px;
    float: right;
    margin-right: $margin-mobile;
}

.a {
    text-decoration: none;
    color: $primary-color;
    margin-left: 15px;
}

.a:active {
    border-bottom: solid $primary-color 1.5px;
}

.selected {
    border-bottom: solid $primary-color 1.5px;
}

.selected:hover {
    border-bottom: 0;
}

.a:hover {
    border-bottom: solid $primary-color 1.5px;
}


@media (max-width: 425px) {
    .span {
        display: block;
    }

    .selectionNav {
        float: left;
        display: block;
        margin-top: 10px;
        margin-bottom: 30px;
        margin-left: $margin-mobile;
    }

    .a {
        margin-left: 0;
        margin-right: 15px;
    }
    
  }