@import "./src/styles/index";
@import "./src/styles/form";

.page {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    flex-grow: 0;
}

.imgContainer {
    margin-right: 50px;
    width: 60%;
}

.alert {
    border-radius: 7px;
    padding: 15px 30px;
    border: solid $secondary-color-green 1.5px;
    display: none;
}

@media (max-width: 1150px) {
    .page {
        width: 100%;
        display: block;
    }
    .imgContainer {
        margin-right:0;
    }
}