@import "./src/styles/index";

.hero {
    padding-top: $margin;
    background-color: $secondary-color-orange;
    height: 100%;
    margin: 0;
    padding: 15px $margin;
    display: flex;
    align-items: center;
}

.h3 {
    margin: 0px 0 30px 0;
}


.divimage {
    width: 37vw;
    overflow: hidden;
}

.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.button {
    padding: 15px 30px;
}

.button:hover {
    color: $secondary-color-orange;
}

.rightSection {
    width: 40%;
}

@media (max-width: 1000px) {
    .rightSection {
        width: 80%;
    }
    .divimage {
        display: none;
    }
    .hero {
        width: 100%;
        padding: 70px 30px;
    }
}

@media (max-width: 700px) {
    .button {
        padding: 10px 20px;
    }
  }